export  function counter(state=0,action){
	switch(action.type){
		case'+':
		return state+1
		case'-':
		return state-1
		default:
		return 10
	}
}

export function add(){
	return {type:'+'}
}
export function incre(){
	return {type:'-'}
}

export function addAsync(){
	return dispatch=>{
		setTimeout(()=>{
			dispatch(add())
		},2000)
	}
}



