import React from 'react'
import {connect} from 'react-redux'
import {NavBar} from 'antd-mobile'
import {Switch,Route} from 'react-router-dom'
import NavFooter from '../navFooter/navFooter.js'
function Boss(){
	return <p>boss</p>
}

function Employee(){
	return <p>Employee</p> 
}

function Msg(){
	return <p>Msg</p>
}

function Me(){
	return <p>Me</p>
}
@connect(
	state=>state.user
	)
class DashBoard extends React.Component{
	render(){
		const navList=[
			{
				path:'/boss',
				text:'老板',
				icon:'boss',
				title:'老板列表',
				component:Boss
			},
			{
				path:'/employee',
				text:'求职',
				icon:'employee',
				title:'求职列表',
				component:Employee
			},
			{
				path:'/msg',
				text:'消息',
				icon:'msg',
				title:'消息列表',
				component:Msg
			},
			{
				path:'/me',
				text:'用户',
				icon:'me',
				title:'用户信息',
				component:Me
			}
		]
		const {pathname}=this.props.location
		return<div>
			<NavBar className='fix-header'>{navList.find(v=>v.path===pathname).title}</NavBar>
			<div style={{marginTop:45}}>
				<Switch>
				{navList.map(v=>{
					<Route path={v.path} component={Boss}></Route>
				})}
				</Switch>
			</div>
			<NavFooter data={navList}></NavFooter>
		</div>
	}
}
export default DashBoard


