import React from 'react'
import {Grid,List} from 'antd-mobile'
import PropTypes from 'prop-types'
class AvatarSelect extends React.Component{
	static propTypes={
		AvatarSelect:PropTypes.func.isRequired
	}
	constructor(props){
		super(props)
		this.state={}
	}
		render(){
		const avatarList='avatar (1),avatar (2),avatar (3),avatar (4),avatar (5),avatar (6),avatar (7),avatar (8),avatar (9),avatar (10),avatar (11),avatar (12)'.split(',')
			.map(v=>({
				icon:require(`../../images/avatar-select/${v}.svg`),
				text:v
			}))
		const gridHeader=this.state.icon?(<div>
						 <span>已选择头像</span>
						 <img style={{width:20}} src={this.state.icon}/>
						 </div>)
						 :<div>请选择头像</div>	
		return<div>
			<List renderHeader={()=>gridHeader}>
				<Grid 
				data={avatarList} columnNum={5}
				onClick={ele=>{
					this.setState(ele)
					this.props.AvatarSelect(ele.text)
				}}
				/>
			</List>
		</div>
	}
} 

export default AvatarSelect


