import React from 'react'
import Logo from '../logo/logo.js'
import {connect} from 'react-redux'
import {List,InputItem,WingBlank,WhiteSpace,Button,Radio} from 'antd-mobile'
import {Redirect} from 'react-router-dom'
import {register} from '../../redux/user.redux.js'
import './register.css'
@connect(
state=>state.user,
{register}
)
class Register extends React.Component{
	constructor(props){
		super(props)
		this.state={
			user:'',
			pass:'',
			repeatpwd:'',
			types:'boss'
		}
		this.handleRegister=this.handleRegister.bind(this)
	}
	handleChange(key,val){
		
		this.setState({
			[key]:val
		})
	}
	handleRegister(){
		this.props.register(this.state);
	}
	render(){
		const RadioItem=Radio.RadioItem
		return <div>
		{this.props.redirectTo?<Redirect to={this.props.redirectTo}/>:null}
					<Logo/>
					<WingBlank>
						<List>
						{this.props.msg?<p className="error_msg">{this.props.msg}</p>:null}
							<InputItem
							onChange={v=>this.handleChange('user',v)}
							>user</InputItem>
							<InputItem
							onChange={v=>this.handleChange('pwd',v)}
							type="password"
							>password</InputItem>
							<InputItem
							onChange={v=>this.handleChange('repeatpwd',v)}
							type="password"
							>confirm</InputItem>
							<RadioItem checked={this.state.types==="boss"}
							onChange={()=>this.handleChange('types','boss')}
							>
							BOSS
							</RadioItem>
							<RadioItem checked={this.state.types==="employee"}
							onChange={()=>this.handleChange('types','employee')}>
							Employee
							</RadioItem>
						</List>
						<Button type="primary" onClick={this.handleRegister}>register</Button>
					</WingBlank>
				</div>
	}
}

export default Register


